				text: '"3月13日（北京）— 全球最大的共享单车平台ofo小黄车宣布完成E2-1轮融资8.66亿美元。本轮融资由阿里巴巴领投，灏峰集团、天合资本、蚂蚁金服与君理资本共同跟投。这是共享单车行业由规模化增长转入精细化运营阶段获得的首次融资，再次创下共享单车行业单笔最高融资纪录。',
