            qr() {
                this.flag = true;
            },
            qrNone() {
                this.flag = false;
            }
        }
    }
</script>

<style scoped>
    * {
        margin: 0px;
        padding: 0px;
    }
    .footer {
        position: relative;
        width: 100vw;
        height: 335px;
    }
    .mark {
        position: relative;
        background-color: #3D3D42;
