    }
    .qr-none {
        position: absolute;
        bottom: 0;
        left: 124px;
        opacity: 0;
    }
    .port {
        width: 94px;
        height: 32px;
        position: absolute;
        left: 50%;
        margin-left: -47px;
        top: 171px;
    }
    .port span {
      cursor: pointer;
    }
    .xl {
        width: 32px;
        height: 32px;
        margin-right:25px;
    }
    .xl svg {
        width: 32px;
    }
    .vx {
        width: 32px;
        height: 32px;
    }
    .vx img {
        width: 32px;
    }


    .location {
        width: 100vw;
